@use "../utils/variables";

.card {
   max-width: 100%;

   // Card Asientos
   &-seats {
      &--num {
         margin: auto;
         width: 200px;

         .input-seat-num {
            display: flex;
            justify-content: center;
            gap: 1rem;
         }

         input {
            background-color: inherit;
            border-width: 0 0 3px 0;
            border-bottom: 3px solid variables.$gray-600;
            border-radius: 0;
            box-shadow: none;
            font-size: 2rem;
            text-align: center;
            width: 50px;
            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
               -webkit-appearance: none;
               margin: 0;
            }
         }
      }
      &--simb {
         margin-bottom: 1rem;
      }
      &--simb,
      &--select {
         width: 360px;
         h3 {
            font-size: 1.25rem;
            font-weight: 500;
         }
      }
   } 

   // Card Itinerario
   &-itinerary {
      background-color: variables.$white;

      .card-body {
         padding: 1rem 8%;
      }

   }

   &-summary {
      width: 100%;

      .card-body {
         padding: 3rem;
      }

      h3 {
         font-size: 1.25rem;
         margin-bottom: 1.5rem;
      }
      h4 {
         font-size: 1rem;
         color: variables.$primary;
         margin-bottom: 0.75rem;
      }
      p {
         line-height: 1.2;
      }
   }
} 
